

.background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: -webkit-gradient(
    linear,
    left bottom, right top,
    from(#1c1810),
    color-stop(#3a3326),
    color-stop(#5c513c),
    color-stop(#7f7155),
    color-stop(#a5936e),
    color-stop(#b3a079),
    color-stop(#c2ad84),
    color-stop(#d1bb8f),
    color-stop(#cab68d),
    color-stop(#c3b18b),
    color-stop(#bcac8a),
    to(#b5a788)
  );
  background-image: linear-gradient(
    to right top,
    #1c1810,
    #3a3326,
    #5c513c,
    #7f7155,
    #a5936e,
    #b3a079,
    #c2ad84,
    #d1bb8f,
    #cab68d,
    #c3b18b,
    #bcac8a,
    #b5a788
  );
}

.login-form {
  width: 100%;
  max-width: 330px;
  padding: 20px;
  margin: auto;
  height: 100%;
  position: relative;
}

:focus {
  outline: none;
}

.login-form input {
  width: 100%;
  height: 100%;
  border: 0;
  padding: 7px 0;
  border: none;
}

.login-form input ~ .focus-border {
  position: absolute;
  left: 25px;
  width: 0;
  height: 2px;
  background-color: #dac292;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}
.login-form input:focus ~ .focus-border {
  width: 80%;
  -webkit-transition: 0.4s;
  transition: 0.4s;
  left: 25px;
}

.login-form input:focus {
  border-color: none;
  box-shadow: none;
  outline: 0 none;
}

.butn-color {
  background-color: #dac292 !important;
  color: black !important;
}

